import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";
import { SupportService } from "app/support/services/support.service";
import { IUserVM } from "app/shared/models/general/general";
import { ActivatedRoute } from "@angular/router";
import { IRefundBalanceVM } from "app/support/models/support";
import { ITransferBalanceMV } from "./../../models/support";

@Component({
  selector: "app-transfer-balance",
  templateUrl: "./transfer-balance.component.html",
  styleUrls: ["./transfer-balance.component.scss"],
})
export class TransferBalanceComponent implements OnInit {
  username: string;
  PaymentReference_Code: string;
  SubmitForm: FormGroup;
  fromUserId: number;
  fromUser: IUserVM;
  toUserId: number;
  toUser: IUserVM;
  constructor(
    private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    private SupportService: SupportService,
    private toastr: ToastrService,
    private route: ActivatedRoute
  ) {
    route.paramMap.subscribe((params) => {
      this.fromUserId = Number(params.get("userId"));
      this.getRefundBalanceData();
    });
  }

  ngOnInit(): void {
    this.SubmitForm = this.fb.group({
      toUserEmail: [null],
    });
  }

  getRefundBalanceData() {
    if (this.fromUserId) {
      this.spinner.show();
      this.SupportService.getRefundBalanceData<IRefundBalanceVM>(
        this.fromUserId
      ).subscribe((res) => {
        this.fromUser = res.user;
        this.spinner.hide();
      });
    }
  }

  SearchUserByEmail() {
    var email = this.SubmitForm.controls["toUserEmail"].value;
    // if (this.fromUser.account_email == email) {
    //   this.toastr.error("Emails cannot be the same");
    //   return;
    // }

    this.SubmitForm = this.fb.group({
      fromUserId: this.fromUser?.user_ID,
      toUserId: this.toUser?.user_ID,
      Email: email,
      PaymentReference: [null]
    });
    if (this.SubmitForm) {
      this.spinner.show();
      this.SupportService.SearchUserByEmail<IRefundBalanceVM>(
        this.SubmitForm.value
      ).subscribe((res) => {
        this.toUser = res.user;
        this.SubmitForm.controls["toUserId"].setValue(this.toUser.user_ID);
        this.spinner.hide();
      });
    }
  }

  TransferBalance() {
    this.SubmitForm.controls["PaymentReference"].setValue(this.PaymentReference_Code);
    if (this.SubmitForm) {
      this.spinner.show();
      this.SupportService.TransferBalance<ITransferBalanceMV>(
        this.SubmitForm.value
      ).subscribe((res: any) => {
        if (res.returnValue == 1) this.toastr.success(res.returnString);
        else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
    }
  }
}
